            <a href=\"/static/files/template.csv\" download>Download the template here.</a>\n                    </div>\n\n                    <div class=\"dropbox m-b-75\">\n                        <vue-dropzone\n                                ref=\"fileUploadZone\"\n                                id=\"dropzone\"\n                                :options=\"dropzoneOptions\"\n                                v-on:vdropzone-sending=\"sendingEvent\"\n                                @vdropzone-success=\"vsuccess\"\n                        ></vue-dropzone>\n                    </div>\n\n                    <button class=\"uploadBtn\"\n                            name=\"Upload Properties\"\n                            value=\"Upload Properties\"\n                            id=\"uploadBtnCsv\"\n                            @click=\"sendProperties\"\n                    >\n                        Upload Properties\n                    </button>\n\n                    <a @click=\"switchBulk()\" class=\"entries\">Enter your properties manually\n                    </a>\n                </div>\n            </section>\n        </template>\n        <template v-else>\n            <SinglePropertyImport @on-done=\"manualDone\"></SinglePropertyImport>\n            <div>\n                <a @click=\"switchBulk()\" class=\"entries\">Import CSV file</a>\n            </div>\n        </template>\n    </vue-modal>\n</template>\n\n<script>\nimport vue2Dropzone from 'vue2-dropzone'\nimport 'vue2-dropzone/dist/vue2Dropzone.css'\nimport SinglePropertyImport from './SinglePropertyImport'\n\nexport default {\n  name: 'import-modal',\n  props: [ 'modalName', ],\n  data () {\n    return {\n      isBulk: true,\n      dropzoneOptions: {\n        url: process.env.API_ENDPOINT + '/properties-bulk/import',\n        thumbnailWidth: 150,\n        maxFilesize: 0.5,\n        addRemoveLinks: true,\n        uploadMultiple: false,\n        headers: { 'access-control-request-headers': '', },\n        acceptedFiles: '.csv',\n        autoProcessQueue: false,\n        dictDefaultMessage: 'Drag files here or click to browse. \\n' +\n          '50MB max file size. All files must be .CSV',\n      },\n    }\n  },\n  components: {\n    vueDropzone: vue2Dropzone,\n    SinglePropertyImport,\n  },\n  methods: {\n    onClose (event) {\n      this.$emit('on-done')\n    },\n    sendProperties () {\n      this.$refs.fileUploadZone.dropzone.processQueue()\n    },\n    sendingEvent (file, xhr, formData) {\n      formData.append('teamId', this.$store.state.teamId)\n    },\n    vsuccess (file, response) {\n      this.$modal.hide(this.modalName)\n    },\n    switchBulk () {\n      this.isBulk = !this.isBulk\n    },\n    manualDone () {\n      this.$modal.hide(this.modalName)\n    },\n  },\n}\n</script>\n\n<style scoped>\n    .dropzone {\n        outline: 2px dashed lightblue; /* the dash box */\n        outline-offset: -10px;\n        color: dimgray;\n        padding: 10px 10px;\n        min-height: 200px; /* minimum height */\n        position: relative;\n        cursor: pointer;\n        background-repeat: no-repeat;\n        background-image: url('/static/images/upload.svg');\n        background-position: center;\n        border: 0px solid rgba(0, 0, 0, 0.3);\n    }\n\n    .input-file {\n        opacity: 0; /* invisible but it's there! */\n        width: 100%;\n        height: 200px;\n        position: absolute;\n        cursor: pointer;\n    }\n\n    .dropzone .dz-message {\n        text-align: center;\n        margin: 10em 0 5em;\n    }\n\n    .dropbox:hover {\n        background: lightblue; /* when mouse over to the drop zone, change color */\n    }\n\n    .dropbox p {\n        font-size: 1.2em;\n        text-align: center;\n        padding: 70px 0 0;\n    }\n\n    .coming-soon {\n        background: #f5f6fA;\n        width: 419px;\n        padding: 25px;\n        margin-left: auto;\n        margin-right: auto;\n        margin-bottom: 35px;\n    }\n\n    .upload-form {\n        position: relative;\n        margin-bottom: 60px;\n    }\n\n    .fs_icon {\n        position: absolute;\n        top: 35px;\n        left: 50%;\n        transform: translateX(-50%);\n    }\n\n    .software {\n        float: left;\n    }\n\n    .software:not(:last-child) {\n        margin-right: 24px;\n        margin-top: 10px;\n    }\n\n    .software-rentpost {\n        width: 59px;\n    }\n\n    .software-resman {\n        width: 56px;\n    }\n\n    .software-yardi {\n        width: 73px;\n    }\n\n    .software-rentmanager {\n        width: 108px;\n    }\n\n    .upload-header {\n        margin-bottom: 35px;\n    }\n\n    .uploadBtn {\n        display: block;\n        margin: 0 auto 40px;\n        text-transform: uppercase;\n        color: #fff;\n        background: #8ec549;\n        padding: 10px 35px;\n        font-weight: 400;\n        letter-spacing: 2px;\n        border: 0;\n    }\n\n    .entries {\n        display: block;\n        text-align: center;\n        margin-bottom: 20px;\n        font-size: 20px;\n        cursor:pointer;\n    }\n\n    .vue-dropzone {\n        transition: background-color 0.2s linear;\n    }\n</style>\n"],"sourceRoot":""}]);

