ate</label>\n                    <input type=\"text\" placeholder=\"KY\" name=\"state\" id=\"state\" v-model=\"currentProperty.state\">\n                  </div>\n                  <div class=\"col-md-6 double-section\">\n                    <label for=\"zip\">Zip Code</label>\n                    <input type=\"text\" placeholder=\"40202\" name=\"zip\" id=\"zip\" v-model=\"currentProperty.zip\">\n                  </div>\n                </div>\n              </div>\n\n            <!-- Country -->\n            <div class=\"form-group col-md-8 col-md-offset-2\">\n              <label for=\"country\">Country</label>\n              <input type=\"text\" placeholder=\"USA\" name=\"country\" id=\"country\" v-model=\"currentProperty.country\">\n            </div>\n\n            <!-- Login Button -->\n            <div class=\"form-group login-button\">\n              <div class=\"col-md-6 col-md-offset-3\">\n                <button type=\"button\" class=\"blue-border btn-link btn-tranparent btns-blue m-b-15\" @click=\"add()\">\n                  Add Address\n                </button>\n              </div>\n            </div>\n\n          </form>\n          <!-- Back & Submit -->\n          <div class=\"form-group\">\n            <div class=\"col-md-12 text-center\">\n              <button type=\"submit\" class=\"btns btns-green\" :disabled=\"saving\" @click=\"done()\">Done</button>\n            </div>\n          </div>\n        </div>\n      </div>\n    </section>\n</template>\n\n<script>\nimport PropertyService from '@/services/PropertyService'\n\nexport default {\n  data () {\n    return {\n      saving: false,\n      properties: [],\n      currentProperty: {\n        address: null,\n        city: null,\n        zip: null,\n        state: null,\n        country: null,\n      },\n    }\n  },\n  mounted () {\n    //\n  },\n  computed: {\n\n  },\n  watch: {\n\n  },\n  methods: {\n    add () {\n      PropertyService.validate(this.currentProperty.address, this.currentProperty.city, this.currentProperty.state).then((response) => {\n        if (response.isValid) {\n          this.properties.push(response.property)\n          this.currentProperty = {\n            address: null,\n            city: null,\n            zip: null,\n            state: null,\n            country: null,\n          }\n        } else {\n          console.log('Invalid address')\n        }\n      })\n    },\n    remove (index) {\n      this.properties.splice(index, 1)\n    },\n    done () {\n      this.saving = true\n      PropertyService.addMultipleProperties(this.$store.state.teamId, this.properties).then((result) => {\n        this.$emit('on-done')\n      }).finally(() => {\n        this.saving = false\n      })\n    },\n  },\n}\n</script>\n\n<!-- Add \"scoped\" attribute to limit CSS to this component only -->\n<style scoped>\n  .properties-added{\n    background-color: #F3F4FA;\n  }\n</style>\n"],"sourceRoot":""}]);

