ppercase;\n    letter-spacing: 2px;\n    padding: 10px 35px;\n}\n.tranparent_btn.btn:hover,\n.tranparent_btn.btn:focus{\n    border: 2px solid #22497c;\n    background-color: #ffffff;\n    color: #22497c;\n}\n.onboarding .back-next .btn.green_btn{\n    margin: 0;\n}\n\n\n/* Fixed topbar header */\n.navbar.fixed-top{\n    background-color: #fff;\n}\n/* Fixed LeftSide Navbar css */\n.navbar-left{\n    background-image: -webkit-gradient(linear, left top, left bottom, from(#24283e), to(#004380));\n    background-image: linear-gradient(#24283e, #004380);\n    height: calc(100% - 68px);\n    left: 0;\n    padding-top: 20px;\n    position: fixed;\n    top: 68px;\n    width: 72px;\n    z-index: 9;\n}\n\n.navbar-left .icon {\n    display: block;\n    width: 72px;\n    height: 72px;\n    margin-bottom: 10px;\n    text-align: center;\n}\n.navbar-left svg {\n    display: inline-block;\n    margin-top: 50%;\n    -webkit-transform: translateY(-50%);\n    transform: translateY(-50%);\n    max-width: 36px;\n}\n.navbar-left .icon.active path,\n.navbar-left .icon:hover path {\n    stroke: #79bfe8;\n}\n.navbar-left .dropright{\n    bottom: 50px;\n    position: absolute;\n    text-align: center;\n    width: 100%;\n}\n.navbar-left .dropdown-toggle img{\n    border: 2px solid #d3e0e9;\n    border-radius: 50%;\n    display: inline-block;\n    padding: 2px;\n    height: 50px;\n    width: 50px;\n}\n.navbar-left .dropright .dropdown-toggle::after{\n    display: none;\n}\n\n\n/* Center block section css */\n.center_section{\n    margin-top: 68px;\n    /*padding: 50px 15px 0;*/\n}\n\n.dropright ul.dropdown-menu {\n    top: 0px;\n    left: 72px;\n}\n\n.dropright:hover ul.dropdown-menu {\n    display: block;\n}\n\n.property-rating {\n    background-color: #cccccc;\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -ms-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n    font-size: 240px;\n    float: none;\n    height: 1em;\n    margin: 0 auto;\n    position: relative;\n    width: 240px;\n}\n.property-rating:after {\n    position: absolute;\n    top: 0.08em;\n    left: 0.08em;\n    display: block;\n    content: \" \";\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -ms-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n    background-color: #f5f5f5;\n    width: 0.84em;\n    height: 0.84em;\n    -webkit-transition-property: all;\n    -moz-transition-property: all;\n    -o-transition-property: all;\n    transition-property: all;\n    -webkit-transition-duration: 0.2s;\n    -moz-transition-duration: 0.2s;\n    -o-transition-duration: 0.2s;\n    transition-duration: 0.2s;\n    -webkit-transition-timing-function: ease-in;\n    -moz-transition-timing-function: ease-in;\n    -o-transition-timing-function: ease-in;\n    transition-timing-function: ease-in;\n}\n.property-rating:hover:after {\n    top: 0.04em;\n    left: 0.04em;\n    width: 0.92em;\n    height: 0.92em;\n}\n.property-rating .info {\n    position: absolute;\n    top: 65px;\n    font-size: 5rem;\n    width: 240px;\n    z-index: 10;\n    text-align: center;\n}\n.property-rating .info span {\n    display: block;\n    white-space: normal;\n}\n.property-rating .info .percentage {\n    font-size: 80px;\n    line-height: 80px;\n    height: 80px;\n    color: #6a6a6a;\n}\n.property-rating .info .label {\n    color: #6a6a6a;\n    font-size: 14px;\n    font-weight: 400;\n    width: 160px;\n    margin: 5px auto 0;\n}\n.property-rating .slice {\n    position: absolute;\n    width: 1em;\n    height: 1em;\n    clip: rect(auto, auto, auto, auto);\n}\n.property-rating .slice .bar{\n    position: absolute;\n    border: 0.08em solid #8ec549;\n    width: 240px;\n    height: 240px;\n    clip: rect(0em, 0.5em, 1em, 0em);\n    -webkit-border-radius: 50%;\n    -moz-border-radius: 50%;\n    -ms-border-radius: 50%;\n    -o-border-radius: 50%;\n    border-radius: 50%;\n    -webkit-transform: rotate(280.8deg);\n    -moz-transform: rotate(280.8deg